html {
	font-family: $font-primary;
	box-sizing: border-box;
	font-size: 16px;
	scroll-behavior: smooth;
}

.container {
	width: 100%;
	margin: 0 1rem 0 1rem;
  
	@include small {
		max-width: 480px;
	}

	@include medium {
		max-width: 768px;
	}

	@include large {
		max-width: 976px;
	}

	@include xlarge {
		max-width: 1440px;
	}
}
